@import url('https://fonts.googleapis.com/css2?family=Merriweather&display=swap');

*{
    font-family: 'Merriweather', serif;
}

.pump-flex{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
}
.pump-img{
    width: 90vw;
    height: 50vh;
    background: url('../images/3.png') no-repeat center;
    background-size: contain;
    margin: 20px 0px 50px 0px;
}

.history{
    text-transform: uppercase;
    font-size: 15px;
}

.line-1 {
    margin-top: 15px;
    width: 93vw;
    align-items: center;
    border: 1px solid brown;
}

.product-cater{
    margin-top: 20px;
    font-weight: bolder;
    text-align: center;
    font-size: 13px;
    font-weight: bolder;
}
.product-details{
    text-align: center;
    margin-top: 20px;
}

.footer-box{
    margin-top: 10px;
    width: 92vw;
    height: 13vh;
    border: 1px solid rgba(227, 78, 78, 0.954);
    background: rgba(227, 78, 78, 0.954);
}
.contact-info-media{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;   
}

.call-media, .facebook-media, .webpage-media{
    padding-top: 30px;
}

.contact-info-media svg,
.contact-info-media span
{
    color: aliceblue;
}
