@import url('https://fonts.googleapis.com/css2?family=Merriweather&display=swap');

*{
    font-family: 'Merriweather', serif;
}

.mainFlex{
    display: flex;
    flex-direction: row;
    margin-top: 40px;
    flex-wrap: wrap;
}
.leftside{
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    width: 35%;
    height: 90vh;
    background: url('../images/1.png') no-repeat center;
    background-size: contain;
}

.rightside{
    width: 65%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.rightside > .award-new-1{
    font-weight: bold;
}
.rightside > .award-new-1 > span{
    text-transform: uppercase;
}
.rightside > ul {
    font-size: 14px;
}

.rightside > .award-pic {
    background: url('../images/2.png') no-repeat;
    background-size: contain;
    background-position: center;
    width: 60vw;
    height: 500px;

}

.rightside > .award-new-2 > .award-name{
    font-weight: bolder;
}
